@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

.MuiToolbar-gutters {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.MuiToolbar-regular {
  min-height: 40px !important;
  background-color: inherit !important;
}
.MuiAppBar-colorPrimary {
  color: inherit !important;
  background-color: rgba($color: $color-background-3, $alpha: 0.5) !important;
}
.app-bar-logo {
  img {
    height: 30px;
  }
}

.MuiPaper-elevation4 {
  box-shadow: none !important;
}

.btn-app-bar {
  font-family: $font-dosis;
  font-weight: 700;
  color: $primary-color;
  border: none;
  text-transform: uppercase;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 1.8em;
  text-align: center;
  text-decoration: none;
  margin-right: 0.3em;
  
}
.btn-app-bar:hover {
  color: $color-white;
}
.btn-app-bar:active {
  color: $color-white;
  background-color: $color-blue;

}

@media screen and (max-width: 599px) {
  .btn-app-bar {
    font-family: $font-dosis;
    font-weight: 700;
    color: $color-black !important;
    border: none;
    text-transform: uppercase;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 1.8em;
    text-align: center;
    text-decoration: none;
    margin-right: 0.3em;
  }
  .btn-app-bar:hover {
    color: $color-white;
    background-color: $color-blue;
  }
}