@import "../../../Assets/scss/Colors.scss";
@import "../../../Assets/scss/Typography.scss";

.footer-section {
  bottom: 1;
  background-color: rgba($color: $color-background-2, $alpha: 0.74);
}
footer {
  padding-top: 5px;
  color: $primary-color;
  margin: auto;
  .center-text {
    text-align: center;
  }
  img {
    height: 40px;
    display: inline;
    margin: auto;
  }

  .footer-icon {
    color: $secondary-color;
    font-size: 3rem;
    text-align: center;
  }
  .MuiTypography-h1 {
    font-family: $font-dosis;
    font-size: 1.4em;
    margin: 0;
    font-weight: 100;
    text-align: left;
    display: inline;
    margin: auto;
    :hover {
      color: $secondary-color;
    }
    :link {
      text-decoration: none;
    }
  }
}
@media screen and (max-width: 599px) {
  footer {
    .desktop-footer {
      display: none;
    }
    .MuiTypography-h1 {
      font-family: $font-dosis;
      font-size: 1.4em;
      margin: 0;
      font-weight: 100;
      text-align: center;
      :hover {
        color: $secondary-color;
      }
      :link {
        text-decoration: none;
      }
    }
    img {
      height: 40px;
      width: 40px;
      padding: 0px 10px 0px 0px;
    }
    .MuiGrid-spacing-xs-4 {
      width: calc(100% + 2px);
      margin: -16px;
    }
  }
}

.btn-footer,
.btn-footer:hover,
.btn-footer:focus {
  font-family: $font-dosis;
  font-weight: 700;
  color: $color-yellow;
  // background-color: $color-yellow;
  border: none;
  text-transform: uppercase;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 1em;
  text-align: center;
  text-decoration: none;
  margin-right: 0.3em;
  // margin-top: 2.5em;
}
.btn-footer-link {
  font-family: $font-dosis;
  font-weight: 500;
  color: $primary-color;
  font-size: 1em;
  text-decoration: none;
  text-align: center;
}
